<template>
    <div>
        <!---->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item>GeoServer</el-breadcrumb-item>
            <el-breadcrumb-item>WFS</el-breadcrumb-item>
            <el-breadcrumb-item>WFS</el-breadcrumb-item>
        </el-breadcrumb>
        <!---->
        <el-card>
            <button id="drawTool" @click="drawClick"></button>
            <div id="map"></div>
        </el-card>
    </div>
</template>
<script>
import Map from 'ol/map';
import View from 'ol/view';
import Point from 'ol/geom/point';
import VectorLayer from 'ol/layer/vector';
import Vector from 'ol/source/vector';
import GeoJSON from 'ol/format/geojson';
import WFS from 'ol/format/wfs';
import Draw from 'ol/interaction/draw';
import Style from 'ol/style/style';
import Circle from 'ol/style/circle';
import Fill from 'ol/style/fill';
import loadingstrategy from 'ol/loadingstrategy';
import {getTdtLayer} from "../../../utils/searchUtils";
export default {
    data() {
        return {
            // 
            geoserverData: {
                wsName: 'webgistest',
                uri: 'http://www.openplans.org/webgistest',
                wfsURL: process.env.VUE_APP_GEOSERVER + 'geoserver/wfs?',
                layer: 'port'
            },
            draw: null
        }
    },
    created() {
    },
    mounted() {
        this.initMap()
    },
    methods: {
        initMap() {
            let that = this
            // WFS  
            let wfsSource = new Vector({
                format: new GeoJSON(),
                url: function (extent) {
                    return (
                        that.geoserverData.wfsURL + 'service=WFS&version=1.1.0&request=GetFeature&typeName=' +
                        that.geoserverData.wsName + ":" +
                        that.geoserverData.layer + '&' + 'outputFormat=application/json&srsname=EPSG:4326&bbox=' +
                        extent.join(',') + ',EPSG:4326');
                },
                strategy: loadingstrategy.bbox
            });

            let wfsLayer = new VectorLayer({
                title: 'add Layer',
                source: wfsSource,
                style: function (feature) {
                    return new Style({
                        geometry: feature.getGeometry(),
                        image: new Circle({
                            radius: 5,
                            fill: new Fill({
                                color: "red"
                            })
                        })
                    })
                },
            })

            let layers = [
              getTdtLayer("vec_w"),
              getTdtLayer("cva_w"), wfsLayer
            ];

            let map = new Map({
                target: 'map',
                layers: layers,
                view: new View({
                    projection: "EPSG:4326",
                    center: [114, 31],
                    zoom: 4
                })
            });

            let draw = new Draw({
                source: new Vector({
                    features: []
                }),
                type: 'Point',
                freehand: false,//,
                stopClick: true
            });
            map.addInteraction(draw);
            draw.setActive(false);

            draw.on("drawend", function (e) {
                draw.setActive(false);
                let feature = e.feature;
                that.addFeature(feature);
                wfsLayer.getSource().changed();
            });
            this.draw = draw
        },
        drawClick() {
            this.draw.setActive(true)
        },
        async addFeature(feature) {
            // 1Feature
            let ft = feature.clone()
            let properties = ft.getProperties()
            properties.address = ""
            properties.name = ""
            properties.porttype = "wnm"
            properties.province = "wnm"
            let coordinates = ft.getGeometry().getCoordinates()
            properties.lat = coordinates[0]
            properties.lng = coordinates[1]
            ft.setProperties(properties)

            let geom = ft.getGeometry()
            // PointOutsideEnvelopeException: 1 outside of (-90.0,90
            // 
            geom.applyTransform((flatCoordinates, flatCoordinates2, stride) => {
                for (let j = 0; j < flatCoordinates.length; j += stride) {
                    let y = flatCoordinates[j]
                    let x = flatCoordinates[j + 1]
                    flatCoordinates[j] = x
                    flatCoordinates[j + 1] = y
                }
            })
            ft.setGeometryName('geom') // postgisgeomshpthe_geom
            ft.setGeometry(geom)
            // 2
            let WFSTSerializer = new WFS();
            let featObject = WFSTSerializer.writeTransaction([ft],
                null, null, {
                featureNS: this.geoserverData.uri,
                featurePrefix: this.geoserverData.wsName,//
                featureType: this.geoserverData.layer,//
                srsName: 'EPSG:4326'
            })
            let serializer = new XMLSerializer()
            let featString = serializer.serializeToString(featObject);
            const { data: res } = await this.$http.post(this.geoserverData.wfsURL,
                featString,
                {
                    headers: {
                        //
                        "Content-Type": "application/xml"
                    }
                })
            if (res.indexOf("Exception") != -1) {
                console.log(res);
                alert("" + res)
            }
            else {
                alert("")
                window.location.reload();
            }
        }
    }
}
</script>

<style lang="less" scoped>

</style>
